.jeju_insert { width:70%; padding:100px 0 20px; margin:0 auto; text-align: center; }
.jeju_insert h2 { padding:0 0 30px; font-size:30px; } 
.jeju_insert ul { display: flex; padding: 0 0 50px; justify-content: center; } 
.jeju_insert ul li { border:1px solid #bbb; flex:1; line-height: 50px; cursor: pointer; } 
.jeju_insert ul li:hover { color:#fff; background-color:skyblue; } 


.jeju_MAP { display: flex; } 
.jeju_img { position:relative; width:1000px; height:500px; margin:0 auto; background: url(./img/ico_map_jeju.jpg) no-repeat center; background-size: cover; } 
.jeju_img div { position:absolute; width:100px; height:100px; font-size: 20px; border-radius: 100%; background:#fff; line-height: 100px; cursor: pointer; } 
.jeju_img div:hover { color:#fff; background-color:skyblue; } 
.jeju_img div.jeju_jungmoon { bottom:78px; left: 280px; } 
.jeju_img div.jeju_seoguipo { right: 473px; bottom: 78px; } 
.jeju_img div.jeju_aewoul { top: 140px; left: 173px; } 
.jeju_img div.jeju_jejusi { top: 59px; left: 447px; } 

.ingi_kewords { padding:0 0 0 50px } 
.ingi_kewords ul { position:relative; padding:0; } 
.ingi_kewords ul li { display: flex; height:100px; border:none; border-radius: 100%; background-color: #ededed; line-height: 1; justify-content: center; align-items: center; margin:10px } 

.clickActive { padding:50px 0 0; } 
.clickActive h2 { text-align: center; font-size: 30px; padding:0 0 50px } 
.clickActive ul { display:flex; justify-content: center; margin:0 auto; text-align: center; width:70%; } 
.clickActive ul li { flex:1; border:1px solid #bbb; line-height: 50px; cursor: pointer;}
.clickActiveLi {background-color: none; } 
.clickActiveLi.active { background-color: #ededed; border-bottom:none}
.section1 { display: flex; width:100%; height:675px; background-color: #ededed;  } 
.section1.active { display: none; } 

